<template>
  <div>
    <Loadings v-if="data ==null"></Loadings>
    <div class="container containerp" v-if="data != null">
      <section class="sales-data">
        <div class="caption flex-fs">
          <!-- <img src="./img/avatar.png" /> -->
          <div>
            <p class="name">{{data.customer.company}}</p>
            <p class="contact">{{data.customer.name}} {{data.customer.phone}}</p>
            <p class="address">{{data.customer.address}}</p>
          </div>
        </div>

        <div class="flex-sa">
          <router-link :to="{ path: '/ordercheck/'+cust_no }">
            <div class="value">{{toThousands(data.customer.total_sum)}}</div>
            <div class="title">销售总额</div>
          </router-link>
          <router-link :to="{ path: '/ordercheck/'+cust_no }">
            <div class="value">{{data.customer.total_order}}</div>
            <div class="title">订单笔数</div>
          </router-link>
        </div>
      </section>

      <section class="customer-analysis">
        <div class="caption flex-sb">
          客户拜访记录
          <router-link :to="{ path: '/nodes/'+cust_no }" class="add">+添加记录</router-link>
        </div>

        <Empty v-if="data != null && data.customer_log.length == 0"></Empty>
        <div class="list" v-else>
          <div class="item" v-for="(item,indexs) in  data.customer_log" :key="indexs">
            <div class="text-content">{{item.content}}</div>
            <div class="flex-sa bottom"> 
            
           
                <span>{{formatDate(item.start_time)}}</span>
       
            </div>
          </div>
        </div>
      </section>
    </div>
  </div>
</template>

<script>
import { getCustomer } from "@api/public";
import Empty from "@components/Empty";
import { Icon } from "vant";
import Loadings from "@components/Loadings";
export default {
  name: "CustDetails",
  components: {
    Empty,
    Loadings,
    [Icon.name]: Icon,
  },
  data: function () {
    return {
      data: null,
      cust_no: "",
    };
  },
  created() {
    this.cust_no = this.$route.params.type;
    getCustomer({ cust_no: this.$route.params.type }).then((res) => {
      this.data = res.data;
    });
  },
  mounted: function () {},
  methods: {
    toThousands(num) {
      var result = [],
        counter = 0;
      num = (num || 0).toString().split("");
      for (var i = num.length - 1; i >= 0; i--) {
        counter++;
        result.unshift(num[i]);
        if (!(counter % 3) && i != 0) {
          result.unshift(",");
        }
      }
      return result.join("");
    },
    formatDate: function (value) {
      let date = new Date(value * 1000);

      let y = date.getFullYear();
      let MM = date.getMonth() + 1;
      MM = MM < 10 ? "0" + MM : MM;
      let d = date.getDate();
      d = d < 10 ? "0" + d : d;

      return y + "-" + MM + "-" + d + " ";
    },
  },
};
</script>
<style scoped>
.text-content {
  line-height: 24px;
  padding-top: 10px;
}
.sales-data .caption {
  color: #fff;
  background: #092e48;
  padding: 19px;
  margin-bottom: 10px;
  font-size: 16px;
}

.sales-data .flex-sa {
  height: 100px;
  border-radius: 5px;
  background: #fff;
  margin: 0 9px;

  box-shadow: 0 0 5px #ccc;
  text-align: center;
}

.sales-data .flex-sa .title,
.to-do .flex-sa .title {
  color: #999;
  font-size: 14px;
  margin-bottom: 10px;
}

.to-do,
.query,
.customer-analysis {
  box-shadow: 0 0 5px #eee;
  background-color: #fff;
  margin: 10px;
  border-radius: 5px;
}

.to-do .caption,
.query .caption,
.customer-analysis .caption {
  font-weight: 600;
  height: 40px;
  line-height: 40px;
  border-bottom: 1px solid #eee;
  padding-left: 10px;

  font-size: 16px;
  color: #666;
}

.to-do .flex-sa {
  text-align: center;
  padding: 30px 0;
}

.caption .month {
  height: 25px;
  width: 60px;
  background: #092e48;
  border-radius: 20px;
  color: #fff;
  line-height: 25px;
  font-size: 12px;
}

.caption .month span {
  display: inline-block;
  width: 23px;
  height: 23px;
  background: #fff;
  border-radius: 50%;
  margin-left: 1px;
  margin-right: 4px;
}

.customer-analysis .list {
  line-height: 43px;
  font-size: 14px;
}

.customer-analysis .list .item {
  padding-left: 8px;
  border-bottom: 5px solid #eee;
  color: #666;
}

.customer-analysis .list img {
  width: 20px;
}

.customer-analysis .list .item .right {
  font-weight: bold;
}

.customer-analysis .list .item .right span {
  font-size: 10px;
  font-weight: 500;
  color: #999;
}

.caption img {
  margin-right: 10px;
  width: 55px;
}

.name {
  color: #fff;
  font-size: 16px;
}

.contact {
  font-size: 16px;
  opacity: 0.9;
  color: #fff;
  margin: 2px 0;
}

.address {
  font-size: 14px;
  opacity: 0.8;
  color: #fff;
}

.value {
  color: #000;
  margin-bottom: 5px;
  font-size: 22px;
}

.value span {
  font-size: 12px;
  color: #999;
}

.add {
  font-size: 12px;
  padding-right: 10px;
  color: #666;
}

.bottom {
  font-size: 12px;
    padding-right: 10px;
    line-height: 30px;
    border-top: 1px solid #eee;
    margin-top: 10px;
}
</style>
